.theme-dark {
  --bkg-color: hsl(204, 15%, 14%);
  --test: hsl(204, 10%, 10%);
  --bkg-color-alpha1: hsla(204, 15%, 80%, 0.1);
  --bkg-color-alpha2: hsla(204, 15%, 80%, 0.2);

  --border-color: hsla(204, 15%, 100%, 0.3);
  --border-color-sec: hsla(204, 15%, 100%, 0.2);

  --text-color: hsla(0, 0%, 100%, 0.9);
  --text-color-inverse: hsla(0, 0%, 0%, 0.9);
  --text-color-secondary: hsla(0, 0%, 100%, 0.7);
  --text-color-tertiary: hsla(0, 0%, 100%, 0.3);
  
  /* PLAIN */
  --plain-color: hsl(204, 5%, 55%);
  --plain-bkg-color: var(--plain-color);
  --plain-bkg-color-sec: hsl(204, 5%, 50%);
  --plain-bkg-color-alpha1: hsla(204, 5%, 60%, 0.12);
  --plain-bkg-color-alpha2: hsla(204, 5%, 60%, 0.3);
  --plain-border-color: var(--plain-color);
  --plain-text-color: hsla(204, 5%, 65%);
  --plain-text-color-inverse: var(--text-color-inverse);

  /* ACCENT */
  --accent-color: hsl(214, 82%, 45%);
  --accent-bkg-color: var(--accent-color);
  --accent-bkg-color-sec: hsl(214, 82%, 50%);
  --accent-bkg-color-alpha1: hsla(214, 82%, 50%, 0.12);
  --accent-bkg-color-alpha2: hsla(214, 82%, 50%, 0.4);
  --accent-border-color: var(--accent-color);
  --accent-text-color: hsl(214, 82%, 50%);
  --accent-text-color-inverse: var(--text-color-inverse);

  /* DANGER */
  --danger-color: hsl(12, 82%, 40%);
  --danger-bkg-color: var(--danger-color);
  --danger-bkg-color-sec: hsl(12, 82%, 45%);
  --danger-bkg-color-alpha1: hsla(12, 82%, 45%, 0.1);
  --danger-bkg-color-alpha2: hsla(12, 82%, 45%, 0.4);
  --danger-border-color: var(--danger-color);
  --danger-text-color: hsl(12, 82%, 45%);
  --danger-text-color-inverse: var(--text-color-inverse);

  /* WARNING */
  --warning-color: hsl(51, 82%, 40%);
  --warning-bkg-color: var(--warning-color);
  --warning-bkg-color-sec: hsl(51, 82%, 45%);
  --warning-bkg-color-alpha1: hsla(51, 82%, 45%, 0.1);
  --warning-bkg-color-alpha2: hsla(51, 82%, 45%, 0.4);
  --warning-border-color: var(--warning-color);
  --warning-text-color: hsl(51, 82%, 45%);
  --warning-text-color-inverse: var(--text-color-inverse);

  /* SUCCESS */
  --success-color: hsl(93, 82%, 35%);
  --success-bkg-color: var(--success-color);
  --success-bkg-color-sec: hsl(93, 82%, 40%);
  --success-bkg-color-alpha1: hsla(93, 82%, 40%, 0.08);
  --success-bkg-color-alpha2: hsla(93, 82%, 40%, 0.4);
  --success-border-color: var(--success-border-color);
  --success-text-color: hsl(93, 82%, 40%);
  --success-text-color-inverse: var(--text-color-inverse);

  /* DISABLED */
  --disabled-color: hsl(240, 1%, 32%);
  --disabled-bkg-color: var(--disabled-color);
  --disabled-bkg-color-sec: hsl(240, 1%, 37%);
  --disabled-bkg-color-alpha1: hsla(240, 1%, 37%, 0.1);
  --disabled-bkg-color-alpha2: hsla(240, 1%, 37%, 0.4);
  --disabled-border-color: var(--disabled-color);
  --disabled-text-color: hsl(240, 1%, 32%);
  --disabled-text-color-inverse: hsla(216, 8%, 0%, 0.5);  
}